$AlmosWhite: hsl(0, 0%, 98%) ;
$MediuGray: hsl(0, 0%, 41%) ;
$AlmosBlack: hsl(0, 0%, 8%);
@import url('https://fonts.googleapis.com/css2?family=Epilogue:wght@500;700&display=swap');
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    list-style-type: none;
    user-select: none;
}
body {
    font-family: 'Epilogue', sans-serif;
    height: 100Vh;
    overflow: hidden;
    background-color: $AlmosWhite;
  
    .main {
       
        filter: drop-shadow(1);
        padding: 30px;
        min-height: 100vh;
        width: 100%;
        .menu {
           
                z-index: 150;
               right: 2%;
               top: 2%;
                width: 32px;
                position: absolute;
                cursor: pointer;
        
                @media screen and (min-width : 480px) {
                    display: none;
                }
            }
        
            .menu-container {
                position: absolute;
                z-index: 100;
                right: 0;
                top: 0;
                opacity: 3 !important;
                height: 100vh;
                width: 65%;
                background-color: whitesmoke;
                text-align: right;
                #feature {
                    cursor: pointer;
                    margin-left: 15px;
                    margin-top: 0;
                    font-size: 1em;
                    img {
                        margin-right: 15px;
                    }
                }
                #compani {
                    margin-left: 15px ;
                    margin-top: 0;
                    font-size: 1em;
                } 
               ul {
                text-align: left;
                margin-left: 35px;
                margin-top: 60px;
                font-size: 1.5em;
                li {
                    margin: 20px 0;
                    color: $MediuGray   ;
                    cursor: pointer;
                }
                #mobile-login {
                    background: none;
                    border: none;
                    font-size: 0.8em;
                    color: $MediuGray;
                   margin-left: 50px;
                    
                    width: 100px;
                    height: 40px;
                    cursor: pointer;
                    transition: 0.3s ease;
                    &:hover {
                            filter: hue-rotate(90deg);
                            opacity: 0.8;
                        }
                }
                #mobile-register {
                    margin-top: 10px;
                   
                    width: 200px;
                    background: none;
                    color: $MediuGray;
                    border: 1px solid $MediuGray;
                    border-radius: 15px;
                    font-size: 0.8em;
                    height: 40px;
                    cursor: pointer;
                        transition: 0.3s ease;
                    &:hover {
                        opacity: 0.8;
                    }
                }
               }
        
                @media screen and (min-width : 480px) {
                    display: none;
                }
            }
        @media screen and (max-width:450px) {
                padding: 0px;
            }
        header {
            position: relative;
            
        nav {
            width: 80%;
            margin: 0 auto;
            display: flex;
            justify-content: space-around;
            @media  screen and (max-width:1850px) {
                width: 90%;
                justify-content: space-between;
            }
                         @media screen and (max-width:450px) {
                             width: 100%;
                         }
            ul {
                display: flex;
                gap: 30px;
                color: $MediuGray;
                width: 50%;
                justify-content: space-evenly;
                @media screen and (max-width:1850px) {
                    width:auto ;
                    justify-content: space-around;
                }
                                @media screen and (max-width:450px) {
                                  visibility: hidden;
                                }
                        
                li {
                    user-select: none;
                    cursor: pointer;
                    transition: 0.2s ease;
                   
                    &:hover {
                        color: $AlmosBlack;
                        transform: scale(1.05);
                    }
                    .arrow {
                        margin-left: 8px;
                        margin-bottom: 3px;
                    }
                                        #logo {
                                            @media screen and (max-width:450px) {
                                               visibility: visible;
                                               padding: 15px
                                            }
                                        }
                }
                #login {
                        border: none;
                        background:none;
                    }
                    #register {
                        transform: translateY(-10px);
                        width:90px;
                        height: 40px;
                        background: none;
                        border: 1px solid $MediuGray;
                        border-radius: 15px;
                    }
                button {
                    color: $MediuGray;
                    cursor: pointer;
                    transition: 0.3s ease;
                    &:hover {
                        opacity: 0.5;
                        transform: scale(1.1);
                    }
                }
            }
        }
        #features {
            position: absolute;
            left: 19%;
            background-color: white;
            border-radius: 15px;
            padding: 15px;
            text-align: left;
            visibility: hidden;
            -webkit-box-shadow: 2px 2px 9px 2px rgba(0, 0, 0, 0.47);
                box-shadow: 2px 2px 9px 2px rgba(0, 0, 0, 0.47);
                @media screen and (max-width: 1850px) {
                    left: 9%;
                }
         li {
            margin: 7px 0;
            img {
               margin-right: 10px;
                width: 15px;
                height: 15px;
            }
         }
        
        }
        #company {
            position: absolute;
            left: 28%;
            background-color: white;
            border-radius: 10px;
            visibility: hidden;
            padding: 20px 15px;
            -webkit-box-shadow: 2px 2px 9px 2px rgba(0, 0, 0, 0.47);
                box-shadow: 2px 2px 9px 2px rgba(0, 0, 0, 0.47);
                @media screen and (max-width: 1850px) {
                    left: 18%;
                }
                                 @media screen and (max-width:1250px) {
                                     left: 25%;
                                 }
            li {
                margin: 5px 0;
            }
        }
    }
    .card {
        display: grid;
        grid-template-columns: 50% 50%;
        width: 100%;
        min-height: 90vh;
        margin: 0 auto;
        @media screen and (max-width:450px) {
               display: block;
            }
        .left {
            text-align: left;
          margin-top: 150px;
          margin-left: 140px;
        
          @media screen and (max-width:1850px) {
            margin-top: 90px;
          }
                   @media screen and (max-width:1600px) {
                       margin-top: 110px;
                       margin-left: 30px;
                   }
                                        @media screen and (max-width:1400px) {
                                            margin-top: 130px;
                                        }
                                                                                 @media screen and (max-width:1250px) {
                                                                                     margin-top: 70px;
                                                                                 }
                                                                                                                                                                  @media screen and (max-width:1025px) {
                                                                                                                                                                      margin-top: 40px;
                                                                                                                                                                  }
                                                                                                                                                                                                                                                                                                                                  @media screen and (max-width:450px) {
                                                                                                                                                                                                                                                                                                                                      text-align: center;
                                                                                                                                                                                                                                                                                                                                      margin: 410px auto 0 auto;
                                                                                                                                                                                                                                                                                                                                  }
            h1 {
                margin-top: 50px;
                font-size: 7em;
                @media screen and (max-width:1850px) {
                        font-size: 6em;
                    }
                                         @media screen and (max-width:1400px) {
                                             font-size: 5em;
                                         }
                                                                                  @media screen and (max-width:1250px) {
                                                                                      font-size: 4em;
                                                                                  }
                                                                                                                                                                  @media screen and (max-width:450px) {
                                                                                                                                                                      font-size: 2.2em;
                                                                                                                                                                  }
            }
            p {
                margin-top: 40px;
                font-size: 2em;
                width: 60%;
                color: $MediuGray;
                @media screen and (max-width:1850px) {
                        font-size: 1.5em;
                        margin-top: 80px;
                    }
                                         @media screen and (max-width:1400px) {
                                            font-size: 1.4em;
                                            width: 70%;
                                         }
                                                                                  @media screen and (max-width:1250px) {
                                                                                      margin-top: 60px;
                                                                                      font-size: 1.3em;
                                                                                  }
                                                                                                                                                             
                                                                                                                                                                   @media screen and (max-width:1025px) {
                                                                                                                                                                       font-size: 1.2em;
                                                                                                                                                                   }
                                                                                                                                                                                                                                                                                                                                   @media screen and (max-width:450px) {
                                                                                                                                                                                                                                                                                                                                       text-align: center;
                                                                                                                                                                                                                                                                                                                                       margin: 15px auto;
                                                                                                                                                                                                                                                                                                                                       font-size: 1em;
                                                                                                                                                                                                                                                                                                                                       width: 100%;
                                                                                                                                                                                                                                                                                                                                   }
            }
           button {
            margin-top: 40px;
            width: 210px;
            height: 80px;
            background-color: $AlmosBlack;
            color: $AlmosWhite;
            border: none;
            border-radius: 10px;
            cursor: pointer;
            transition: 0.2s ease;
            @media screen and (max-width:1850px) {
                    width: 160px;
                    height: 50px;
                    margin-top: 80px;
                }
                                 @media screen and (max-width:1250px) {
                                    width: 140px;
                                    margin-top: 60px;
                                 }
                                                                 @media screen and (max-width:450px) {
                                                                     text-align: center;
                                                                     margin: 10px auto;
                                                                 }
            &:hover {
                opacity: 0.8;
                transform: scale(1.05);
            }
           }
           ul {
            display: flex;
            margin-top: 120px;
            justify-content: space-around;
            width: 100%;
            @media screen and (max-width:1400px) {
                    margin-top: 130px;
                }
                                 @media screen and (max-width:1250px) {
                                     margin-top: 100px;
                                 }
                                                                  @media screen and (max-width:480px) {
                                                                      margin-top: 10px;
                                                                    
                                                                  }
                            
            li {
                img {
                    width: 190px;
                    @media screen and (max-width:1850px) {
                           width: 140px;
                        }
                                                 @media screen and (max-width:1400px) {
                                                    width: 110px;
                                                 }
                                                                                                  @media screen and (max-width:1250px) {
                                                                                                      width: 90px;
                                                                                                  }
                                                                                                                                                                                                  @media screen and (max-width:480px) {
                                                                                                                                                                                                      width: 60px;
                                                                                                                                                                                                  }
                                                                                            
                }
           }
           }
        }
        .right {
            display: flex;
            align-items: center;
            justify-content: center;
           img {
            max-width: 600px;
            width: 100%;
            z-index: 0;
            @media screen and (max-width:1400px) {
                    max-width: 550px;
                }
                                 @media screen and (max-width:1250px) {
                                     max-width: 450px;
                                 }
                                                                  @media screen and (max-width:1025px) {
                                                                     max-width: 420px;
                                                                  }
                                                                                                                                  @media screen and (max-width:450px) {
                                                                                                                                      max-width: 300px;
                                                                                                                                      
                                                                                                                                      position: absolute;
                                                                                                                                      top: 8%;
                                                                                                                                  }
           }
        }
    }
}
}
